@font-face {
  font-family: 'Yu Gothic';
  src: local('Yu Gothic Medium');
  font-weight: 400;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Helvetica Neue', 'Segoe UI', 'Yu Gothic', YuGothic, sans-serif;
  font-variant-numeric: tabular-nums;
  -webkit-font-feature-settings: 'tnum';
          font-feature-settings: 'tnum';
  font-weight: 500;
}

img {
  height: auto;
  max-width: 100%;
}

.js-modal-scrollLock {
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
}

[data-overlay] {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 3;
  display: none;
}

[data-modal] {
  z-index: 4;
  pointer-events: none;
  display: none;
}

[data-modal].-visible {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
}

.c_modal_inner {
  background-color: #ffffff;
  border-radius: 5px;
  padding: 1em;
  pointer-events: auto;
  position: relative;
  display: none;
  box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.25);
}

.c_modal_inner > [data-modal-close] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  border-radius: 0;
  outline: none;
  background: transparent;
  padding: 0;
  position: absolute;
  right: -20px;
  top: -20px;
  overflow: hidden;
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 1;
  transition: opacity 300ms ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.c_modal_contents {
  max-width: 50vw;
  max-height: calc(70vh - 50px);
  overflow-y: auto;
  -webkit-overflow-scrolling: auto;
}

.c_modal_contents div {
  margin: 0 0 10em;
}

.c_modal_contents input,
.c_modal_contents textarea {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  border-radius: 0;
  outline: none;
  background: transparent;
  padding: 0;
  padding: 0.5em;
  border: 1px solid #aaaaaa;
  width: 100%;
  background: #fafafa;
}

.c_modal_contents textarea {
  height: 300px;
}

.c_modal_contents button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  border-radius: 0;
  outline: none;
  background: transparent;
  padding: 0;
  opacity: 1;
  transition: opacity 300ms ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5em;
  width: 100%;
  color: #ffffff;
  background-color: #0080ff;
  cursor: pointer;
}

body {
  padding: 1em;
  background-color: #f0f0f0;
  background-image: url(/images/sample.jpg);
}

img {
  display: none;
}

